<template>
    <div class="popuBox">     
        <div style="height: 100%; width: 90%; padding: 45px 15px">
            <el-collapse  v-model="activeNames" @change="handleChange">
                <el-collapse-item :key="item.guid" v-for="item in forms" :title="item.FormTemplate.title" :name="item.FormTemplate.name">
                    <VueForm ref="form" v-model="item.data"  :schema="item.FormTemplate.template" @on-cancel="onCancel"  :formProps="{labelPosition:'left'}" :formFooter="{okBtn:'提交',cancelBtn:'取消'}">
                    </VueForm>
                </el-collapse-item>
            </el-collapse>
        </div>
    <div>
        <el-button type="primary" @click="onSubmit">提交</el-button>
    </div>
    </div>
</template>

<style scoped>
.popuBox  {
  color: rgb(0, 150, 255);
}
</style>


<style >

.el-form-item__content{
    margin-left: 40%;
}
.formFooter_item{
    display: none;
}

</style>

<script>
import VueForm from '@lljj/vue-json-schema-form';
import {getForms,getformstemplatebygroup,GUID} from "../api/forms";
import {savelocalforms,submitForm,removelocalforms} from "../api/forms"
import { Notify } from 'vant';
//可配置化表单组件，采用vuejsonschemaforms组件实现jsonschema,这里UI采用内置的elementui，兼容移动UI
//扩展折叠UI，以支持多组schema
export default {
    components:{VueForm},
    inject: ["eventHub"],
    data() {
        return {
            //是否已经提交过，如果是提交状态，将不再进行缓存。
            ispushed:false,
            showforms: true,
            activeNames:"",
            //默认值，创建新表单时会使用
            defaultValues:{},
            forms:[
                {
                    name:"测试form1",
                    guid:"baca1",
                    data: {},
                    FormTemplate: {
                        type: 'object',
                        required: [
                            'userName',
                            'age',
                        ],
                        properties: {
                            userName: {
                                type: 'string',
                                title: '用户名',
                                default: 'Liu.Jun',
                            },
                            age: {
                                type: 'number',
                                title: '年龄'
                            },
                            bio: {
                                type: 'string',
                                title: '签名',
                                minLength: 10,
                                default: '知道的越多、就知道的越少',
                                'ui:options': {
                                    placeholder: '请输入你的签名',
                                    type: 'textarea',
                                    rows: 1
                                }
                            },
                            imgUrlList: {
                                title: "图片上传",
                                type: "array",
                                "ui:widget": "UploadWidget",
                                //action指定上传的接口
                                "ui:action": "https://run.mocky.io/v3/78638190-1adb-4d32-8f6e-400068ffe286",
                                "default": [
                                ],
                                "items": {
                                    "type": "string"
                                }
                            }
                        }
                    },
                    uiSchema: {},
                }
            ],
        }
    },
    mounted(){
        //调用此接口进行交互
        window.onSubmit=this.onSubmit.bind(this);
        this.eventHub.$on("clickformedit", this.onclick);
        //获取router的参数group来获取一组表单
        let group = this.$route.params.group;
        let that=this;
        if (group) {
            getForms(1, 10, group).then(dt => {
                
                if (dt.isok) {
                    if (dt.data && dt.data.length) {
                        this.forms = dt.data;
                        //默认展示第一张表单
                        if (this.forms && this.forms.length > 0) {
                            that.activeNames = this.forms[0].FormTemplate.name;
                        }
                    } else {
                        throw "no data"
                    }

                } else {
                    throw "no data"
                }
            })
            .catch(err=>{
                
                that.getTemplate();
            })
            window.formguid=group;
            return;
        }
       
    },
    methods:{
        //没有查询到数据的情况下，需要查询模板，相当于联合了显示和新建功能
        getTemplate() {
            let that=this;
            //获取模板名称
            let tgroup = this.$route.query.group;
            if (tgroup) {
                getformstemplatebygroup(tgroup).then(dt => {
                    if (dt.isok) {
                        //创建一个group用的guid
                        if (!window.formguid) {
                            let groupguid = GUID();
                            window.formguid = groupguid;
                        }
                        this.forms = dt.data.map(i => {
                            return {
                                FormTemplate: i,
                                data: window.defaultValues ? window.defaultValues : {},
                                name: GUID(),
                                group: window.formguid,
                                FormTemplateId: i.id,
                            }
                        });
                        if (this.forms && this.forms.length > 0) {
                            that.activeNames = this.forms[0].FormTemplate.name;
                        }
                    }
                })
            }
        },
        handleChange(){

        },
        onCancel(){
            this.eventHub.$emit("clickform",true);
        },
        onSubmit(){
            let isValidall = true;
            let allvalidate = this.$refs.form.forEach(i => {
                 i.$refs.genEditForm.validate((isValid, resData) => {
                    isValidall = isValidall && isValid
                });
            })
            if (!isValidall) {
                Notify({ type: 'primary', message: '请补充表单！' });
                return;
            }
            //经过验证后方可提交
            submitForm(this.forms).then(dt=>{
                if(dt.isok)
                {
                    Notify({ type: 'primary', message: '提交成功！' });
                    //提交成功后移除此条数据
                    removelocalforms(this.formData).then(isok=>{
                        this.eventHub.$emit("clickform",true);
                    })
                }else
                {
                    Notify({ type: 'primary', message: '提交失败！' });
                }
            })
        },
        //关闭后返回到列表
        handlecloseicon(){
            this.eventHub.$emit("clickform",true);
        },
        //将一些操作封装到其内部，tools采用Eventhub和其子组件通讯,所有toolbar均采用此方式
        onclick(item){
            this.showforms = !this.showforms;
            if (item) {
                this.formData = item;
            }
        }
    },
    watch:{
         //监控数据发生变化时自动保存到localstorage中，下次打开app将自动读取,
        'formData': {
            handler: function (newval, oldval) {
                //默认采用localstorage
                //savelocalforms(newval);
            },
            deep:true
        }
    },
    beforeDestroy(){
        this.eventHub.$off("clickformedit",this.onclick);
    }
}
</script>
